table {
    font-family: arial;
    border-collapse: collapse;
    width: 100%;
    margin-bottom: 2em;
    
  }
  
  td{
    border: 1px solid black;
    text-align: left;
    padding: 1em;
  }

  th {
      background-color: #f8e5e5;
      border: 1px solid black;
      text-align: left;
      padding: 1em;
  }
  
.bioInfo{
    /* text-align: left;
    text-align: top; */
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    margin-top: 0.25em;
    margin-bottom: 0.25em;
    margin-left: 0;
    
}
.GitHubChart{
    margin-left: 0.10em;
    margin-top: 0.25em;

}

.LinkedIn:hover{
    background-color:lightblue;
  mix-blend-mode: hard-light;
  color:purple;
}
.GitHubIcon{
  width:7em;
}
.GitHubIcon:hover{
    background-color:#ffffff;
    mix-blend-mode: hard-light;
  color:#1f1616;
}

